import React, { Component } from 'react'
import { Calendar } from 'antd'

class CalendarComponent extends Component {
  componentDidMount() {
    let iterationCount = document.getElementsByClassName('ant-fullcalendar-cell').length

    for(let i = 0;i <= iterationCount;i++) {
      document.getElementsByClassName('ant-fullcalendar-cell')[i]
    }
  }

  render() {
    return (
      <div>
        <Calendar></Calendar>
      </div>
    )
  }
}

export default CalendarComponent